.preloader_type_image {
  margin: 0 auto;
  max-width: 100%;
  margin: 0 auto;
  object-fit: cover;
}

@media screen and (min-width: 600px) {
  .preloader_type_image {
    max-width: 50%;
    margin: 5vh auto;
  }
}

@media screen and (min-width: 800px) {
  .preloader_type_image {
    max-width: 30%;
    margin: 10vh auto;
  }
}