.popup {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(0 0 0 / 0.5);
  visibility: hidden;
  opacity: 0;
  transition: .7s;
  transition-property: visibility, opacity;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
}